.donor-form {
  width: 300px;
  background: #ffa8a8;
  margin: auto;
  text-align: left;
  padding: 10px;
  border-radius: 10px;
  position: absolute;
  z-index: 201;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.donor-form input {
  text-align: left;
}

.nav-class {
  background: #ffa8a8;
  padding: 10px;
  position: absolute;
  z-index: 200;
  width: 100%;
}

.success-alert {
  background: #5cb85c;
  display: inline-block;
  padding: 17px 100px;
  border-radius: 8px;
}

.success-alert i {
  margin-right: 5px;
  background: white;
  border-radius: 50%;
  padding: 3px;
}

.donor-info {
  background: #ffa8a8;
  padding: 10px;
  border-radius: 10px;
}

.info-div {
  position: absolute;
  background: white;
  width: 700px;
  padding: 10px 0px;
  border-radius: 5px;
  right: 50px;
  height: 95px;
  top: -10px;
}

.password-input {
  position: absolute;
  top: 0;
  left: 0;
  text-align: left;
}
